* {
    box-sizing: border-box;
  }
  
  html,
  #root,
  #root > div,
  div[data-reactroot] {
    height: 100%;
    padding: 0;
    margin: 0;
  }
  main{
    text-align: center; 
    padding-left: 5%;
    padding-right: 5%;
}

.btn{
    background-color:#41004D;
    color:lavender;
}

.btn:hover{
    background-color: #9172ec;
    color: #7D187e;
    font-weight: Bold;
}

body,
html {
    font-family: 'Handlee', cursive;
    color: #41004D;
    margin: auto;
}
